.wrapper {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;

    input {
        width: 100%;
    }
}

.test42 {
    display: flex;
    flex-wrap: wrap;
}

.input-wrapper {
    width: 100%;
    background-color: gray;
    padding: 2px;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;

    &__item {
        display: flex;
        flex-wrap: nowrap;
        font-size: 12px;
        margin: 2px;
        background-color: green;
    }

    &__item-close {
        //height: 10px;
        //width: 10px;
        background-color: transparent;
        border: none;
        cursor: pointer;

        &:after {
            display: inline-block;
            content: "\00d7";
        }
    }

    &__chevron-down {
        &:after {
            display: inline-block;
            content: "\25bc";
        }
    }
}

.users {
    position: relative;
    display: flex;
    flex-direction: column;

    &__list {
        position: relative;
        display: block;
        width: 100%;
        list-style-type: none;
        padding: 0;
        margin: 0;
        //top: 29px;

        &__item {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid rgba(157, 157, 157, 0.62);

            &:nth-child(2n) {
                background-color: rgba(101, 124, 241, 0.32);
            }
            &:nth-child(2n + 1) {
                background-color: rgba(79, 104, 246, 0.49);
            }
        }
    }
}

.display-none {
    display: none;
}

.checked-user {
    background: rgba(106, 201, 110, 0.72) !important;
}
